body {
  font-family: "Poppins", sans-serif;
  margin: 0;
}
nav {
  height: 70px;
  display: flex;
  padding: 20px 35px;
  height: 30px;
  box-shadow: 8px 2.9px 7.45px rgba(0, 0, 0, 0.1);
}

.nav--logo {
  max-width: 100px;
}

.collage {
  max-width: 400px;
  align-self: center;
}
.content {
  padding: 20px 30px;
}

.hero {
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.cat-card {
  box-shadow: 3px 3px 6.9px 7.45px rgba(0, 0, 0, 0.2);

  width: 185px;
  margin: 20px 20px;
  display: block;
}
.cat-card h2 {
  font-size: 20px;
  padding: 5px 10px;
}

.profile-pic {
  width: 100%;
}

.card-info img {
  height: 30px;
}
.card-info {
  display: flex;
  align-self: center;
  padding: 5px 10px;
}
.card-info p {
  margin: 5px 15px;
}

.contacts {
  display: flex;
  flex-direction: row;
}
